import React, { useEffect } from 'react'
import './AllYear.css'
import ScrollReveal from 'scrollreveal'

const AllYear = () => {
  useEffect(() => {
    ScrollReveal().reveal('.all-year', {
      distance: '110px',
      origin: 'bottom',
      duration: 1000,
      easing: 'ease',
      reset: true,
    });
    ScrollReveal().reveal('.all-year > *', {
      distance: '20px',
      origin: 'bottom',
      duration: 500,
      easing: 'ease',
      reset: true,
      interval: 80,
    });
  }, []);
  return (
    <div className='all-year'>
      <h1>Vtejte v na kavrn Avantra!</h1>
      <h2>kva, co pot</h2>
      <p>Jsme tuln a modern msto, kde mete v klidu posedt a vychutnat si lek aromatick kvy nebo dalch lahodnch npoj. Nabzme pjemnou atmosfru ideln pro setkn s pteli, pracovn schzky nebo chvle odpoinku. Nae kva je peliv vybran a pipravovan s lskou, abyste si u ns mohli vychutnat opravdov zitek.</p>
    </div>
  )
}

export default AllYear