import React, { useRef, useEffect } from 'react'
import './About.css'
import { animatePhotoDown } from '../../utils/animations.js';
import kafe from '../../assets/kafe.webp'
import mourek from '../../assets/mourek.webp'
import { initScrollReveal } from '../../utils/scrollRevealInit.js';

const About = () => {
  const sectionRef = useRef(null);

  useEffect(() => {
    requestAnimationFrame(() => {
      initScrollReveal();
    });
  }, []);

  useEffect(() => {
    const timeout = setTimeout(() => {
      animatePhotoDown(sectionRef.current);
    }, 100);
    return () => clearTimeout(timeout);
  }, []);

  useEffect(() => {
    animatePhotoDown(sectionRef.current);
  }, []);
  return (
    <div className='about' ref={sectionRef}>
      <div className='about-hero'>
        <img src={`${process.env.PUBLIC_URL}/images/aboutHero.webp`} alt="About hero" className='about-img' />
      </div>
      <div className='about-text'>
        <p>Kavrna Avantra se nachz v malebnm msteku elezn Brod, znmm svou sklskou tradic a krsnou prodou. Najdete ns kousek od eky Jizery a historickho centra. Jsme idelnm mstem k odpoinku po prochzce mstem. Nabzme vbrovou kvu z domc prarny APe Kany, dezerty, limondy a pjemnou atmosfru s vhledem na okol.</p>
      </div>
      <div className='about-us-gallery'>
        <img src={kafe} alt='kafe' />
        <img src={mourek} alt='mourek' />
        <img src={`${process.env.PUBLIC_URL}/images/main.webp`} alt='kafe' />
      </div>
      <div className='about-text'>
        <p>Nae kavrna byla prvn kavrnou v eleznm Brod a jsme ve funkci ji vce ne 7 let. Nachzme se hned vedle pamtkov rezervace Trvnky. Kavrna je pes lto otevena kad den od 11 do 20 hodin, s celoron stlej otevrac dobou 11 a 18 hodin. Nabzme pjemn a klidn venkovn posezen v klidn sti eleznho Brodu u eky Jizery.</p>
      </div>
      <div className='about-text'>
        <h6>a co vce?</h6>
        <p>Pokud spchte, rdi Vm kvu i nco jinho pipravme s sebou. Zahrdka je bez obsluhy, je tedy poteba si pijt objednat na bar. Objednvku samotnou ale rdi pineseme a ke stolu. Pokud doma nemte mlnek, nelamte si hlavu, kvu Vm rdi nameleme.</p>
      </div>
    </div>
  )
}

export default About