import React, { useEffect } from 'react'
import './Menu.css'
import kavaIMG from '../../../assets/kafe.webp'
import zakusekIMG from '../../../assets/zakusek.webp'
import ScrollReveal from 'scrollreveal'

const Menu = () => {
  useEffect(() => {
    ScrollReveal().reveal('.menu-text', {
      distance: '50px',
      origin: 'bottom',
      duration: 1000,
      easing: 'ease',
      reset: true,
    });
  }, []);
  return (
    <div className='menu'>
      <div className='menu-flex'>
        <img src={kavaIMG} className='menu-image' alt='kafe' loading="lazy" />
        <div className='menu-text'>
          <h3>nejen kafe</h3>
          <h4>kva, limondy, hork okolda, vno ...</h4>
          <p> Nae nabdka npoj je irok a jist vm uspokoj vechny chuov pohrky. Pipravujeme kvu z kvalitnch zrn prarny APe Kany, jejich aromatick chu vs pjemn nalad na cel den. Krom toho tu najdete i irok vbr ledovch kv, osvujcch limond, vna a dalch specialit.</p>
        </div>
      </div>
      <div className='menu-flex'>
        <img src={zakusekIMG} className='menu-image' alt='zakusek' loading="lazy" />
        <div className='menu-text'>
          <h3>zkusky</h3>
          <h4>Dorty, zkusky, zmrzlina</h4>
          <p>Pro ty, kte by chtli nco na zub, mme tak irokou klu dezert i zkusk.</p>
        </div>
      </div>
    </div>
  )
}

export default Menu